.seleccionar-usuario {
    width: 100%;
}

.marcar-controles,
.marcar-checklists {
    width: 50%;
}

.marcar-controles {
    padding-right: 4%;
}

.marcar-checklists {
    padding-left: 4%;
}

form {
    font-size: 1rem;
}

select {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    min-width: 300px;
    font-size: 0.9rem;
    border: 1px solid #fbc02d;
    border-radius: 0;
    padding: 0.3rem 0.6rem;
	background: url('../images/keyboard_arrow_down.svg') no-repeat 97% 50% #fff9c4;
    outline: 0;
}



